import Title from "./Title"
import Search from "./Search"
import Notice from "./Notice"

export default function Header() {
    const title = "Home"
    return (
        <>
            <div className="w-full flex bg-gray-300">
                <Notice />
                <div>
                <Title title={title} />
                {/* <Search /> */}
                </div>
            </div>
        </>
    )
}